/* **************** VARIABLES ****************** */
/* **************** PAGE COMPONENTS ****************** */
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1000;
  padding: 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90px;
  transition: all .2s;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .header {
    padding: 0 15px;
  }
}
.header__fixed {
  background: rgba(0, 0, 0, 0.3);
  height: 60px;
}
.header__left-block {
  display: flex;
}
.header .logo {
  display: inline-block;
  margin-right: 20px;
  position: relative;
}
.header .logo:before {
  content: "";
  position: absolute;
  top: 50%;
  right: -17px;
  transform: translateY(-50%);
  height: 21px;
  width: 1px;
  background: #fff;
  opacity: 0.2;
}
.header .lang {
  display: flex;
  align-items: center;
  color: #fff;
  position: relative;
}
.header .lang .current-lang {
  position: relative;
  cursor: pointer;
  padding: 3px 16px 3px 8px;
  transition: background .2s;
}
.header .lang .current-lang:before {
  content: "";
  position: absolute;
  right: 5px;
  top: 55%;
  transform: translateY(-50%);
  border: 4px solid transparent;
  border-top: 4px solid #fff;
  transition: transform .2s;
}
.header .lang .current-lang:hover {
  background: rgba(255, 255, 255, 0.2);
}
.header .lang .current-lang.show {
  background: rgba(255, 255, 255, 0.2);
}
.header .lang .current-lang.show:before {
  transform: translateY(-100%) rotate(180deg);
}
.header .lang__list {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
}
.header .lang__item {
  color: #fff;
  padding: 3px 16px 3px 8px;
  background: rgba(255, 255, 255, 0.2);
  width: 100%;
  display: inline-block;
  transition: background .2s;
}
.header .lang__item:hover {
  background: rgba(255, 255, 255, 0.3);
}
.header__right-block {
  display: flex;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .header__right-block {
    transform: translateX(100%);
    z-index: 12;
    position: fixed;
    max-width: 320px;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    background: #0051b2;
    text-align: center;
    padding: 35px 0;
    transition: transform .3s;
    flex-direction: column;
    padding: 54px 25px;
  }
}
.header__menu {
  display: flex;
  position: relative;
}
.header__menu:before {
  content: "";
  position: absolute;
  top: 50%;
  right: -23px;
  transform: translateY(-50%);
  height: 30px;
  width: 1px;
  background: #fff;
  opacity: 0.3;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .header__menu {
    flex-direction: column;
    padding-bottom: 15px;
    margin-bottom: 15px;
  }
  .header__menu:before {
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 1px;
  }
}
.header__link {
  color: #fff;
  transition: color 0.2s, border-color 0.2s;
  padding: 6px 11px;
  font-size: 15px;
  margin-left: 5px;
  display: inline-block;
}
.header__link:hover {
  color: #1e2945;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .header__link {
    margin-left: 0;
    font-size: 18px;
    margin-bottom: 10px;
  }
  .header__link:hover {
    color: #fff;
  }
}
.header .login__block {
  margin-left: 36px;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .header .login__block {
    display: flex;
    flex-direction: column;
    margin-left: 0;
  }
}
.header .login {
  border: 1px solid #fff;
  border-radius: 5px;
}
.header .login:hover {
  border: 1px solid #1e2945;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .header .login:hover {
    border: 1px solid #fff;
  }
}
.header .mobile_btn {
  cursor: pointer;
  display: none;
  font-size: 0;
  height: 31px;
  margin: 0;
  overflow: hidden;
  padding: 0;
  transition: background .3s;
  width: 40px;
  position: relative;
}
.header .mobile_btn:focus {
  outline: none;
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .header .mobile_btn {
    display: block;
  }
}
.header .mobile_btn span {
  background: #fff;
  display: block;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 3px;
  left: 50%;
  width: 100%;
}
.header .mobile_btn span:after {
  background-color: #fff;
  content: "";
  display: block;
  height: 3px;
  left: 0;
  position: absolute;
  width: 100%;
  bottom: -10px;
}
.header .mobile_btn span:before {
  background-color: #fff;
  content: "";
  display: block;
  height: 3px;
  left: 0;
  position: absolute;
  width: 100%;
  top: -10px;
}
.header .btn_close {
  display: none;
  position: absolute;
  left: 11px;
  top: 24px;
  width: 40px;
  height: 40px;
  opacity: 0.7;
  cursor: pointer;
  transition: opacity .2s;
  z-index: 7;
}
.header .btn_close:hover {
  opacity: 1;
}
.header .btn_close:before,
.header .btn_close:after {
  position: absolute;
  left: 50%;
  content: ' ';
  height: 35px;
  width: 3px;
  background-color: #fff;
}
.header .btn_close:before {
  transform: translateX(-50%) rotate(45deg);
}
.header .btn_close:after {
  transform: translateX(-50%) rotate(-45deg);
}
@media only screen and (min-width: 768px) and (max-width: 1024px), only screen and (max-width: 767px) {
  .header .btn_close {
    display: block;
  }
}
